.B2T {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
}

.B2T img {
  -webkit-filter: invert(80%);
          filter: invert(80%);
  margin-top: 30px;
  transition: all 0.2s ease-in-out;
}

.B2T:hover img {
  -webkit-transform: translateY(-15px);
          transform: translateY(-15px);
}

